@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  min-height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  min-width: 320px;
  max-width: 1150px;
}

html {
  height: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

.container {
  padding-left: 30px;
  padding-right: 30px;
}
@media screen and (min-width: 768px) {
  .container {
    padding-left: 49px;
    padding-right: 49px;
  }
}

.main-header {
  background-color: transparent;
  position: relative;
}
@media screen and (min-width: 768px) {
  .main-header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-image: linear-gradient(#ffffff 88px, rgba(237, 237, 237, 0.3) 88px, rgba(237, 237, 237, 0.3));
  }
}
@media screen and (min-width: 1150px) {
  .main-header {
    grid-template-columns: 1fr 259px 1fr;
  }
}

.main-header__wrapper {
  display: flex;
  padding: 22px 33px;
  position: relative;
  height: 79px;
}
@media screen and (min-width: 768px) {
  .main-header__wrapper {
    height: 95px;
    padding: 0;
    grid-column: 1/2;
    grid-row: 1/2;
  }
}
@media screen and (min-width: 1150px) {
  .main-header__wrapper {
    grid-column: 2/3;
  }
}

.main-header__nav {
  position: absolute;
  width: 100%;
  z-index: 3;
  display: none;
  left: 0;
  top: 0;
}
@media screen and (min-width: 768px) {
  .main-header__nav {
    position: static;
    display: block;
    background-color: transparent;
  }
}

.main-header__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 4;
}
@media screen and (min-width: 768px) {
  .main-header__logo {
    left: 49px;
    transform: translate(0, -50%);
  }
}
@media screen and (min-width: 1150px) {
  .main-header__logo {
    left: 53px;
  }
}

.main-header__button {
  display: block;
  background-image: url("../img/stack.svg#menu-open");
  width: 20px;
  height: 14px;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  margin-left: auto;
  z-index: 5;
  position: absolute;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
}
.main-header__button:hover {
  opacity: 0.6;
}
.main-header__button:active {
  opacity: 0.3;
}
@media screen and (min-width: 768px) {
  .main-header__button {
    display: none;
  }
}

.main-header__logo-image {
  max-width: none;
}

.main-header__toggle-closed {
  background-image: url("../img/icons/menu-close.svg");
  width: 22px;
  height: 22px;
  border: none;
  background-repeat: no-repeat;
  background-position: center;
}

.no-js .main-header__nav {
  display: block;
  position: static;
  z-index: 5555;
  margin-top: 0;
}

.no-js .main-header__button {
  display: none;
}

.main-nav {
  position: relative;
  grid-column: 1/3;
  grid-row: 1/3;
}

@media screen and (min-width: 768px) {
  .main-nav__wrapper {
    display: grid;
    grid-template-columns: 321px 83px 1fr;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__wrapper {
    grid-template-columns: 1fr 259px 440px;
  }
}

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.main-nav__list--desktop {
  display: none;
}
@media screen and (min-width: 1150px) {
  .main-nav__list--desktop {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
}
@media screen and (min-width: 768px) {
  .main-nav__list--navigation {
    grid-column: 2/4;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-wrap: wrap;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__list--navigation {
    padding-right: 49px;
    grid-column: 3/4;
  }
}
@media screen and (min-width: 768px) {
  .main-nav__list--catalog {
    display: flex;
    grid-column: 1/2;
    grid-row: 2/3;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__list--catalog {
    grid-row: 1/2;
    grid-column: 1/2;
  }
}

.main-nav__item {
  position: relative;
  padding: 26px 26px 26px 76px;
  text-align: left;
  background-color: #ffffff;
  box-shadow: 0px 1px 0px 0px #ececec, 0px 1px 0px 0px #ececec inset;
}
@media screen and (min-width: 768px) {
  .main-nav__item {
    box-shadow: none;
    background-color: transparent;
  }
  .main-nav__item--catalog {
    padding: 20px 0;
  }
  .main-nav__item--knitting {
    padding: 20px 0;
  }
  .main-nav__item--basket {
    padding: 32px 0 32px 83px;
  }
  .main-nav__item--search {
    width: 82px;
    padding: 44px 14px 44px 12px;
  }
  .main-nav__item--search::after {
    position: absolute;
    content: "";
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    left: 0;
    top: 0;
    height: 100%;
  }
  .main-nav__item--basket::after {
    position: absolute;
    content: "";
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    left: 0;
    top: 0;
    height: 100%;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__item--catalog {
    padding: 32px 0;
  }
  .main-nav__item--knitting {
    padding: 32px 0 32px 41px;
  }
  .main-nav__item--new {
    padding: 20px 0;
  }
  .main-nav__item--sale {
    padding: 20px 0 20px 45px;
  }
}

.main-nav__item-paragraph {
  display: none;
  margin: 0;
}
@media screen and (min-width: 768px) {
  .main-nav__item-paragraph {
    display: block;
    grid-column: 3/4;
    grid-row: 2/3;
    color: #444;
    text-align: right;
    font-family: inherit;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    padding: 20px 0;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__item-paragraph {
    padding-right: 49px;
  }
}

.main-nav__item-link {
  position: relative;
  color: #000000;
  font-family: inherit;
  font-size: 17px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  text-decoration: none;
}
.main-nav__item-link::before {
  content: "";
  position: absolute;
  width: 18px;
  height: 18px;
  background-repeat: no-repeat;
  background-position: center;
  top: 50%;
  left: -44px;
  transform: translateY(-50%);
}
.main-nav__item-link--search::before {
  background-image: url("../img/stack.svg#search");
}
@media screen and (min-width: 768px) {
  .main-nav__item-link--search::before {
    left: 17px;
  }
}
.main-nav__item-link--basket::before {
  background-image: url("../img/stack.svg#basket");
  width: 21px;
}
@media screen and (min-width: 768px) {
  .main-nav__item-link--basket::before {
    left: -53px;
  }
}
@media screen and (min-width: 768px) {
  .main-nav__item-link--catalog {
    display: block;
    font-size: 15px;
    width: 169px;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__item-link--catalog {
    font-size: 17px;
    width: 179px;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__item-link--new {
    display: block;
    font-size: 15px;
    width: 174px;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__item-link--sale {
    font-size: 15px;
  }
}

@media screen and (min-width: 768px) {
  .main-nav__item-link-span--search {
    display: none;
  }
}

.menu-nav__opened {
  display: block;
}

.hero {
  background-image: url(../../img/background/hero-mobile.jpg);
  width: 100%;
  height: 373px;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  padding: 73px 30px 0 30px;
}
@media screen and (min-width: 768px) {
  .hero {
    background-image: url(../../img/background/hero-tablet.jpg);
    height: 746px;
  }
}
@media screen and (min-width: 1150px) {
  .hero {
    background-image: url(../../img/background/hero-desktop.jpg);
  }
}

.hero__title {
  color: #000;
  text-align: center;
  font-family: Open Sans;
  font-size: 27px;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
}